window.addEventListener("DOMContentLoaded", function(){
    ejecutarApp();
});

function ejecutarApp(){
    crearGaleria();
    scrollNav();
}
function scrollNav(){
    const enlaces = document.querySelectorAll(".enlace");
    //Deslizar al dar clic en la navegacion con el efecto smooth
    enlaces.forEach(e => {
        e.addEventListener("click", function(e){
            e.preventDefault();
            const seccion = document.querySelector(e.target.attributes.href.value);
            seccion.scrollIntoView({behavior:"smooth"});
            seccion.addEventListener("scroll", function(e){
                console.log(e.getBoundingClientRect());
            })
        });
    });
    //Fijar barra de navegacion
    const seccion = document.querySelector("#lineup");
    const navegacion = document.querySelector(".header");
    const body = document.querySelector("body");
    window.addEventListener("scroll", function(){
        if(seccion.getBoundingClientRect().top < 0){
            navegacion.classList.add("fijar-barra");
            body.classList.add("mover-body");
        }else{
            navegacion.classList.remove("fijar-barra");
            body.classList.remove("mover-body");
        }
    })
}
function crearGaleria(){
    const galeria = document.querySelector(".galeria-contenedor");
    for (let i = 1; i <= 12; i++) {
        const picture = document.createElement("PICTURE");
        picture.innerHTML = `
                    <source srcset="build/img/thumb/${i}.avif" type="image/avif">
                    <source srcset="build/img/thumb/${i}.webp" type="image/webp">
                    <img id=${i} src="build/img/thumb/${i}.jpg" alt="Vocalista">
        `
        picture.onclick = function(e){
            agrandarImg(e);
        }
        galeria.appendChild(picture);
    }
}
function agrandarImg(e){
    //Creaci√≥n de etiqueta picture y almacenamiento de imagen
    const picture = document.createElement("PICTURE");
    const id = e.target.attributes.id.value;
    picture.innerHTML = `
                    <source srcset="build/img/grande/${id}.avif" type="image/avif">
                    <source srcset="build/img/grande/${id}.webp" type="image/webp">
                    <img id=${id} src="build/img/grande/${id}.jpg" alt="Vocalista">
        `
    //---------------------------------------------------------
    //Cerrar imagen
    const cerrar = document.createElement("P");
    cerrar.textContent = "X";
    picture.appendChild(cerrar);
    picture.onclick = function(){
        body.removeChild(overlay);
        body.classList.remove("fijar-body");
    }
    //--------------------------
    //overlay oscuro que agrega la imagen almacenada en picture y la cierra
    const overlay = document.createElement("DIV");
    overlay.classList.add("overlay2");
    overlay.appendChild(picture);
    overlay.onclick = function(){
        body.removeChild(overlay);
        body.classList.remove("fijar-body");
    }
    //----------------------------
    //Agregar overlay al body
    const body = document.querySelector("body");
    body.classList.add("fijar-body");
    body.appendChild(overlay);
}